








































	export default {
		data() {
			return {
				step2: false,
				phone: '',
				isAgree: false,
				user_id: '',
				sms_token: '',
				timer: 59,
				isSend: false
			}
		},
		onLoad(options) {
			if(options.id){
				this.user_id = options.id
			}else{
				this.user_id = this.$store.state.userId
			}
		},
		methods: {
			getPhone(e) {
				// console.log(e)
				this.phone = e.detail.value
			},

			getCode(e) {
				var that = this
				var phoneReg = /^1[3-9]\d{9}$/
				if (!phoneReg.test(this.phone)) {
					return uni.showToast({
						title: '请输入正确的手机号',
						icon: 'none'
					})
				}
				this.$api.get('/api/sms/send', {
					mobile: this.phone
				}, function(res) {
					console.log(res)
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
					if (res.data.code == 200) {
						that.isSend = true
						that.countDown()
					}
				})
			},
			excode(e) {
				var that = this
				this.$api.get('/api/sms/check', {
					mobile: this.phone,
					captcha: e.detail.value.code,
					user_id: this.user_id
				}, function(res) {
					console.log(res)
					if (res.data.code == 200) {
						uni.showToast({
							title: '注册成功！'
						})
						setTimeout(function() {
							uni.switchTab({
								url: '/pages/tabbar/tabbar-4/tabbar-4'
							})
						}, 500)
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						})
					}
				})
			},

// 			formSubmit(e) {
