// 			},

			//勾选协议
			checkAgree(e) {
				this.isAgree = !this.isAgree
			},

			//计时器
			countDown() {
				var that = this
				var timer = this.timer
				var interval = setInterval(function() {
					timer--
					that.timer = timer
					if (timer <= 0) {
						that.timer = 59
						that.isSend = false
						clearInterval(interval)
					}
				}, 1000)
			},
		}
	}
